body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
#searchbar h1{
  padding: 0px;
  margin: 0px;
  margin-bottom: 10px;
}

#searchInput {
  height: 50px;
  width: 100%;
  text-align: center;
  margin-bottom: 10px;
}

.searchList {
  list-style: none;
  padding: 0;
}

.searchList li {
  padding: 3px 2px;
  margin: 3px 0px;
  cursor: pointer;
  border-radius: 3px;
}

.searchList li:hover,
.searchList li.selected {
  background-color: lightgray;
  border-left: 4px solid gray;
}

.port-popup .header {
  margin: 0 0 5px 0;
}

.port-popup .leaflet-popup-content-wrapper {
  text-align: center;
}

.port-popup .popup-button {
  width: 100%;
}
.leaflet-control-zoom {
  display: none
}

.leaflet-container {
  height: -webkit-fill-available;
  width: -webkit-fill-available;
}
.schedule-view-list {
  list-style: none;
  font-size: 16px;
}

.schedule-view-list li {
  margin: 5px;
}

.schedule-view-list li span{
  margin: 0px 5px;
}

.card {
  display: inline-block;
  margin: 5px;
  vertical-align: top;
}
#booking-view {
  display: inline-block;
}

#booking-content {
  display: inline-block;
  padding: 30px;
  border: 3px solid #d3d3d3cc;
  border-radius: 5px;
  text-align: left;
  background-color: white;
}

#booking-content .booking-content-input {
  margin-bottom: 20px;
}

#picker-popover {
  z-index: 2000000 !important;
}

#menu-time {
  z-index: 2000000 !important;
}
.my-snackbar {
  z-index: 100000 !important;
  width: 350px;
}

.my-inner-snackbar {
  position: relative;
  left: 20px;
  bottom: 20px;
}
#app-panel {
  position: absolute;
  z-index: 10000;
  background: rgba(255, 255, 255, 0.7);
  border-radius: 5px;
  padding: 25px;
  height: 100%;
  width: 300px;
  -webkit-transition: width 0.5s;
  transition: width 0.5s;
}

#app-panel.full-overlay {
  width: 100%;
  background: rgba(255, 255, 255, 0.9);
  text-align: center;
}

#back-button {
  text-align: left;
}
#portx-header-logo {
  height: 100px
}

.navbar {
  margin-bottom: 0px !important;
  background: #eee; 
}

.navbar-brand {
  padding-top: 5px;
}
